import axios from "axios";
import router from "../router/index";
import store from "../store/index";
import Qs from "qs";
import { ElNotification } from "element-plus";
import { loadingHelper } from "./loading";

//
axios.defaults.withCredentials = true;
//"/proxy"
//process.env.VUE_APP_URL
axios.defaults.baseURL = process.env.VUE_APP_URL;
axios.defaults.headers["token"] = null;
axios.defaults.timeout = 100000; //
//axios.defaults.retry = 1;
//axios.defaults.retryDelay = 100000;
//
let pending = []; //
let cancelToken = axios.CancelToken;
let removePending = (ever) => {
  for (let p in pending) {
    if (pending[p].u === ever.url + "&" + ever.method) {
      pending[p].f(); //
      pending.splice(p, 1); //
    }
  }
};
//boundarytransformRequest
axios.defaults.transformRequest = [
  (data, config) => {
    if (data instanceof FormData) return data;
    switch (config["Content-Type"]) {
      case "application/json": {
        return JSON.stringify(data);
      }
      case "application/merge-patch+json": {
        return JSON.stringify(data);
      }
      case "multipart/form-data": {
        let params = new FormData();
        for (let key in data) {
          params.append(key, data[key]);
        }
        return params;
      }
      default: {
        return Qs.stringify(data);
      }
    }
  },
];
axios.interceptors.request.use(
  (config) => {
    let token = sessionStorage.getItem("TOKEN");
    if (token) {
      config.headers["token"] = token;
    }
    removePending(config); //ajax
    config.cancelToken = new cancelToken((c) => {
      store.commit("pushRequestToken", { cancelToken: c });
      // ajax&
      let date = new Date();
      pending.push({
        u: config.url + "&" + config.method + "&" + date.getTime(),
        f: c,
      });
    });
    return config;
  },
  (err) => {
    loadingHelper.hide();
    ElNotification.error({
      message: "",
      type: "error",
      position: "bottom-right",
    });
    router.replace({
      //
      path: "/login",
      query: {
        redirect: router.currentRoute.value.fullPath,
      }, // path
    });
    return Promise.resolve(err);
  }
);
//
axios.interceptors.response.use(
  (res) => {
    removePending(res.config);
    //HTTP200500
    if (res.data.code !== 0) {
      loadingHelper.hide();
      //(http200)
      ElNotification.error({
        message: res.data.msg,
        type: "error",
        position: "bottom-right",
      });
      return res.data;
    }
    //
    return res.data;
  },
  (err) => {
    if (err && err.response) {
      loadingHelper.hide();
      let response = err.response;
      if (response.status == 504 || response.status == 404) {
        ElNotification.error({
          message: "",
          type: "error",
          position: "bottom-right",
        });
      } else if (response.status == 403) {
        //
        ElNotification.error({
          message: "",
          type: "error",
          position: "bottom-right",
        });
        router.replace({
          //
          path: "/login",
          query: {
            redirect: router.currentRoute.value.fullPath,
          }, // path
        });
      } else if (response.status == 401) {
        //
        ElNotification.error({
          message: "", //
          type: "error",
          position: "bottom-right",
        });
        router.replace({
          //
          path: "/login",
          query: {
            redirect: router.currentRoute.value.fullPath,
          }, // path
        });
      } else if (response.data.code == 500) {
        ElNotification.error({
          message: response.data.msg,
          type: "error",
          position: "bottom-right",
        });
      } else if (response.status == 500) {
        if (response.data.code) {
          ElNotification.error({
            message: response.data.msg,
            type: "error",
            position: "bottom-right",
          });
        } else {
          ElNotification.error({
            message: response.request.responseText,
            type: "error",
            position: "bottom-right",
          });
        }
      } else {
        if (response.data.code) {
          ElNotification.error({
            message: response.data.msg,
            type: "error",
            position: "bottom-right",
          });
        }
      }
    }
    return Promise.reject(err);
  }
);
//postform
//let params = new FormData();for(var key in data){params.append(key,data[key]);}
export const postForm = (url, params) => {
  return axios({
    method: "post",
    url: encodeURI(url),
    data: Qs.stringify(params),
    headers: {
      "Content-Type": "application/x-www-form-urlencoded",
    },
  });
};
//post
export const post = (url, params) => {
  return axios({
    method: "post",
    url: encodeURI(url),
    data: params,
    headers: {
      "Content-Type": "application/json",
    },
  });
};
